import React from 'react'
import Layout from '../components/layout'
import BreadCrumb from '../components/bread-crumb'
import SEO from '../components/seo'
import Navigation from '../components/navigation'
import Slides from '../components/slides'
import { Grid, Divider } from '@material-ui/core'
import { Link } from 'gatsby-theme-material-ui'

const Main = ({ pageContext }) => {
  const {
    navigation,
    title,
    slides,
    breadCrumb,
    pages,
    pdf,
    slug,
    content,
    thumbPath,
    showTitle,
  } = pageContext

  return (
    <Layout>
      <Navigation navigation={navigation} />
      <SEO title={title} />
      <BreadCrumb title={title} breadCrumb={breadCrumb} />
      {showTitle && <h1>{title}</h1>}
      {pages && (
        <ul>
          {pages.map(page => (
            <li key={page.slug}>
              {slug ? (
                <Link
                  style={{ color: 'blue' }}
                  className={`link`}
                  to={`/${slug}/${page.slug}`}
                >
                  {page.title}
                </Link>
              ) : (
                <Link
                  style={{ color: 'blue' }}
                  className={`link`}
                  to={page.slug}
                >
                  {page.title}
                </Link>
              )}
            </li>
          ))}
        </ul>
      )}
      {pdf && (
        <Grid>
          <div key={`body`} dangerouslySetInnerHTML={{ __html: content }} />
        </Grid>
      )}
      {slides && <Slides slides={slides} thumbPath={thumbPath} />}
      <div
        style={{
          height: `35px`,
        }}
      ></div>
      <Divider />
    </Layout>
  )
}

export default Main
