import React from 'react'
import { Grid } from '@material-ui/core'
import SlideCard from './slide-card'

const Slides = ({ slides, thumbPath }) => (
  <Grid
    container
    spacing={1}
    direction="row"
    justify="flex-start"
    alignItems="baseline"
  >
    {slides.map((slide, i) => (
      <SlideCard slide={slide} key={i} thumbPath={thumbPath} />
    ))}
  </Grid>
)

export default Slides
