import React from 'react'
import {
  Typography,
  CardContent,
  CardActions,
  Card,
  makeStyles,
  Paper,
} from '@material-ui/core'
import { Link, Button } from 'gatsby-theme-material-ui'

const useStyles = makeStyles({
  root: {
    width: 320,
    height: 350,
    position: 'relative',
  },
  title: {
    fontSize: 14,
  },
  pos: {
    marginBottom: 12,
  },
  paper: {
    margin: 5,
  },
  actions: {
    position: 'absolute',
    bottom: '0px',
  },
})

const SlideCard = ({ slide, thumbPath }) => {
  const classes = useStyles()
  return (
    <Paper className={classes.paper}>
      <Card className={classes.root}>
        <CardContent>
          <Typography variant="caption" component="h1">
            <strong>
              {slide.index} {slide.title}
            </strong>
          </Typography>
          <Typography
            className={classes.title}
            color="textSecondary"
            gutterBottom
          >
            <img
              style={{
                maxHeight: '191px',
                width: 'auto',
              }}
              className={`slide-thumbnail`}
              src={`${thumbPath}${slide.index
                .toString()
                .replace(/\s/g, '')}.jpg`}
              alt={slide.title}
            />
          </Typography>
          <Typography variant="caption" component="h1">
            {slide.info}
          </Typography>
        </CardContent>
        <CardActions className={classes.actions}>
          <Button size="small">
            <Link
              style={{ color: 'blue' }}
              className={`link`}
              to={`/${slide.slug}`}
            >
              Viewer
            </Link>
          </Button>
        </CardActions>
      </Card>
    </Paper>
  )
}

export default SlideCard
